body > header, body > footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between; }

article header {
  margin-bottom: 1.5em; }

article header h1 {
  font-size: 1.8em;
  margin: 0 0 .1em; }

h1 {
  margin-top: 1em; }

h2 {
  font-size: 1.3em;
  margin-top: 1em;
  margin-bottom: 0; }

nav {
  margin: .5em -.8em; }

nav a {
  margin: .5em .8em; }
